.how-it-works-working-parent {
  margin-top: 20vh;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.how-it-works-working-parent >span:nth-child(1){
  font-size: 30px;
}
.how-it-works-working-parent-mobile >span:nth-child(1){
  font-size: 25px;
}
.how-it-works-working-parent-mobile{
  
  margin-top: 30%;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.badjes{
  display: flex;
  width: 80%;
  align-items: center;
  justify-content: center;
}
.badjes-mobile{
  display: flex;
  width: 80%;
  flex-direction: column;
  gap:20px
}
.how-it-works-badges {
  background: rgb(160, 0, 217);
  background: linear-gradient(
    76deg,
    rgba(160, 0, 217, 1) 0%,
    rgb(246, 37, 18) 47%,
    rgba(231, 13, 223, 1) 98%
  );
  width: 40px !important;
  height: 40px !important;
  padding: 20px;
}
.avtar-card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 16px;
}
.avtar-card-mobile {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
}
.avtar-title-sub-wrap{
  padding: 15px;
  display: flex;
  flex-direction: column;
}
